import { generateText } from "ai";
import { openai } from "@/lib/openai";
import { traceable } from "langsmith/traceable";

/**
 * Sub-agent that specializes in research and analysis tasks
 * This agent is called as a tool from the main agent
 */
export const researchAgent = traceable(
  async (query: string): Promise<string> => {
    const result = await generateText({
      model: openai("gpt-4o-mini"),
      system: `You are a specialized research assistant. Your job is to provide detailed,
      well-researched answers to questions. Focus on accuracy, clarity, and depth.
      Keep responses concise but informative.`,
      prompt: query,
    });

    return result.text;
  },
  { name: "research_agent", run_type: "llm" }
);
